.post-manage-btns {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  visibility: hidden;
}

.post-card {
  background: #fff;
  border-radius: 10px;
  padding: 15px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  cursor: pointer;
}

.post-card:hover {
  .post-manage-btns {
    visibility: visible;
    animation: fadeInAnimation ease 0.5s;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.loader {
  width: 10px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: rgb(2, 188, 2);
  box-shadow: 0 0 0 0 #0004;
  animation: l1 1s infinite;
  margin: 7px 0 0 23px;
}

@keyframes l1 {
  100% {
    box-shadow: 0 0 0 20px #0000;
  }
}

.model-footer {
  display: flex;
  gap: 10px;
}

.leave-pod-btn {
  margin: 0;
  padding: 5px;
  background: red;
  border: none;
  border-radius: 3px;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  visibility: hidden;
}

.pod-card:hover {
  .leave-pod-btn {
    visibility: visible;
    animation: fadeInAnimation ease 0.5s;
  }
}

@import url(https://fonts.googleapis.com/css?family=Roboto:300);
html {
  height: 90%;
}

.back {
  margin: 1em auto;
  font-family: "Roboto";
}
.back span {
  font-size: 2em;
  color: #fff;
  background: #262b37;
  display: table-cell;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.3), 0 5px 0 #ccc;
  padding: 0 15px;
  border-radius: 10px;
  line-height: 80px;
  animation: jumb 2s infinite;
}
@keyframes jumb {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-30px);
    box-shadow: 0 15px 0 rgb(84, 207, 248);
  }
  100% {
    transform: translateY(0px);
  }
}
.back span:nth-child(1) {
  animation-delay: 0s;
}
.back span:nth-child(2) {
  animation-delay: 0.1s;
}
.back span:nth-child(3) {
  animation-delay: 0.2s;
}
.back span:nth-child(4) {
  animation-delay: 0.3s;
}
.back span:nth-child(5) {
  animation-delay: 0.4s;
}
.back span:nth-child(6) {
  animation-delay: 0.5s;
}
.back span:nth-child(7) {
  animation-delay: 0.6s;
}

.global-loader {
  position: absolute;
  /* border: 5px solid red; */
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  background-color: white;
  /* filter: blur(2px); */
}

.select-pod{
  border: 2px solid blue !important;
}